import React, { useEffect, useState, useContext } from 'react'
import { useLocation, useHistory, Link, useParams } from 'react-router-dom'
import Slider from "react-slick";
import _isObject from 'lodash/isObject'
import axios from 'axios'
import './style.css'
import './responsive.css'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import _sample from 'lodash/sample'
import _get from 'lodash/get'
import ListSize from './components/ListSize'
import Button from '../../Components/Button'
import WpCaption from './components/WpCaption'
import { ProviderApp } from '../../ProviderGlobal'
import ProductItem from '../../Components/ProductItem';
const Detail = () => {
    const [sizeActive, setSizeActive] = useState()
    const [productSlider, setProductSlider] = useState([])
    const [productRender, setProductRender] = useState()
    const history = useHistory()
    const products = useContext(ProviderApp)
    const { setCartProducts } = products
    const { id } = useParams()
    // check xem c sn phm hay ko khng th a v {}
    const checkProduct = productRender || {}
    // i qua number  s dng toLocaleString
    const newDel = +checkProduct.del
    const newPrice = +checkProduct.price
    const checkObject = _isObject(productRender)
    useEffect(() => {
        axios.get("https://64a41a3cc3b509573b571155.mockapi.io/api/product/products")
            .then(function (respons) {
                const products = respons.data
                const newProduct = products.find((item) => item.id == id)
                // c sn phm th render khng c a ra trang 404
                if (newProduct) {

                    setProductRender(newProduct)
                } else {
                    history.push({
                        pathname: "/404 "
                    })
                }
                // ramdom 7 sn phm ngu nhin   render slick slider
                setProductSlider([_sample(products), _sample(products), _sample(products), _sample(products), _sample(products), _sample(products), _sample(products)])

            })

    }, [id])
    const handleToCart = (product) => {
        // to 1 sn phm  thm vo cart
        const newProduct = {
            ...product,
            quantity: 1,
            size: sizeActive

        }
        if (sizeActive) {
            setCartProducts((prev) => {

                const check = prev.find(item => item.id === product.id && item.size === sizeActive)
                if (check) {
                    // kim tra xem trong cart c sn phm  vi size y cha 
                    //  c ri th cng thm s lng quantity
                    const newProductCart = prev.filter(item => {
                        if (item.name === product.name && item.size === sizeActive) {
                            item.quantity += 1
                            return item
                        }
                        return item
                    })
                    return [...newProductCart]

                } else {
                    // cha c trong cart th thm mi
                    return [...prev, newProduct]
                }
            })
        } else {
            alert("vui lng chn size")
            return
        }
        alert("t hng thnh cng")

    }
    const settings = {

        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 1,
        initialSlide: 0,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        infinite: true,
        speed: 500,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    infinite: true,

                }
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    initialSlide: 2,
                    infinite: true,
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    infinite: true,
                }
            }
        ]
    };
    const dataSize = [
        {
            id: 1,
            size: 37.5
        },
        {
            id: 2,
            size: 38
        },
        {
            id: 3,
            size: 38.5
        },
        {
            id: 4,
            size: 39
        },
    ]
    const handleToSrollTop = () => {
        window.scroll({
            top: 0
        })
    }
    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={className}
                style={{ ...style, display: "block", right: "10px", zIndex: "2" }}
                onClick={onClick}
            />
        );
    }

    function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
            <div
                className={className}
                style={{ ...style, display: "block", left: "10px", zIndex: "2", }}
                onClick={onClick}
            />
        );
    }

    return (
        <>
            {

                checkObject === true
                    ?
                    <div className='page-detail'>
                        < div className='rs-navbar-b' >
                            <div className='container'>
                                <div className='navbar-b-inner'>
                                    <Link to="/">Trang ch</Link>
                                    <span className='delimiter'>
                                        <i className="fa-solid fa-caret-right"></i>
                                    </span>
                                    <span>Sn phm</span>
                                    <span className='delimiter'>
                                        <i className="fa-solid fa-caret-right"></i>
                                    </span>
                                    <span className='name-tab'>
                                        {checkProduct.name}
                                    </span>
                                </div>
                            </div>

                            <div className='container'>
                                <div className='product-detail'>
                                    <div className='product-detail-img'>
                                        <img src={checkProduct.img}></img>
                                    </div>
                                    <div className='product-detail-summary'>
                                        <h2 className='summary-title'>{checkProduct.name}</h2>
                                        <div className='pr-price-main'>
                                            <strong>Gi :</strong>
                                            <del>{newDel.toLocaleString()}</del>
                                            <span className='pr-price mg-l-1'>
                                                <span className='car-price'>{newPrice.toLocaleString()} </span>
                                            </span>

                                        </div>
                                        <p className='rs-pr'>
                                            <strong>M SP : </strong>
                                            #FY4080
                                        </p>
                                        <p className='rs-pr'>
                                            <strong>Hng SX :</strong>
                                            <Link to="/nike">nike</Link>
                                        </p>
                                        <p className='rs-pr'>
                                            <strong>Tnh Trng : </strong>
                                            Cn hng
                                        </p>
                                        <p className='rs-pr'>
                                            <strong>chn size : </strong>
                                        </p>
                                        <div className='Product-list-Size'>
                                            <ListSize dataSize={dataSize} setSizeActive={setSizeActive} sizeActive={sizeActive} />
                                        </div>
                                        <div className='rs-pr-contact'>
                                            <h3>H tr t hng</h3>
                                            <div className='rs-pr-contact-inner dl-fl'>
                                                <div className='phones dl-fl'>
                                                    <span className='icon'>
                                                        <i className="fa-solid fa-mobile-screen-button"></i>
                                                    </span>
                                                    <span className='info-phone'>
                                                        <span className='info'> Bin: <a href=''>0326953021</a></span>
                                                        <span className='info'> Bin: <a href=''>0326953021</a></span>
                                                    </span>

                                                </div>
                                                <div className='skypes'>
                                                    <span className='info-phone'>
                                                        <span className='info'> Bin: <a href=''>0326953021</a></span>
                                                        <span className='info'> Bin: <a href=''>0326953021</a></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                        <Button className="add-to-cart" onClick={() => handleToCart(checkProduct)} >t hng</Button>
                                    </div>
                                </div>
                            </div>
                            <div className='info-product'>
                                <div className='container'>
                                    <div className='info-product-inner'>
                                        <h3 className='info-product-title'>
                                            Thng tin sn phm
                                        </h3>
                                        <p>
                                            <Link className='hv' to="/adidas">Giy th thao {checkProduct.brand}</Link>
                                            Giy {checkProduct.name} m sn phm FY0306. Hon ton yn tm Cn tt c cc hot ng hng ngy vi i giy Adidas Ultra Boost , i giy duy nht mang li s n nh tuyt i v cm gic thoi mi, va vn khng th nhm ln i u c..
                                        </p>
                                        <p>
                                            <Link className='hv' to="/adidas">Giy th thao {checkProduct.brand}</Link>
                                            Giy {checkProduct.name} m sn phm FY0306. Hon ton yn tm Cn tt c cc hot ng hng ngy vi i giy Adidas Ultra Boost , i giy duy nht mang li s n nh tuyt i v cm gic thoi mi, va vn khng th nhm ln i u c..
                                        </p>
                                        <p>
                                            <b>CNG NGH NI BT: </b>
                                        </p>
                                        <p>ADIDAS LEP</p>
                                        <p>Cng ngh Torsion System mi gip  chu un ca mi giy tng thm 15%, cho si bc thm n hi.</p>
                                        <p>HON TR NNG LNG TUYT VI</p>
                                        <p>6% ht Boost tng cng sn sng bng n nng lng trong tng bc chn.</p>
                                        <p>PRIMEBLUE</p>
                                        <p>Sn phm ny may bng vi cng ngh Primeblue, cht liu ti ch hiu nng cao c s dng si Parley Ocean Plastic. 50% thn giy lm bng vi dt, 75% vi dt bng si Primeblue. Khng s dng polyester nguyn sinh.</p>

                                        {checkProduct.listImg ? <WpCaption dataWpCaption={checkProduct.listImg} name={checkProduct.name} /> : null}
                                    </div>
                                </div>
                            </div>
                            <div className='pro-slider '>
                                <div className='container'>
                                    <div className='pro-slider-inner '>
                                        <div className='carousel-inner'>
                                            <h3 className='info-product-title'>
                                                RELATED PRODUCTS
                                            </h3>
                                            <Slider
                                                {...settings}
                                            >
                                                {
                                                    productSlider.map(item => <ProductItem key={item.id} item={item} handleToSrollTop={handleToSrollTop} />)
                                                }
                                            </Slider>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div >
                    </div >
                    :
                    <div className='loadding'>

                        <div className='loader-inner'>
                            <img src="https://img.idesign.vn/2018/10/23/id-loading-1.gif"></img>
                        </div>


                    </div>
            }
        </>





    )
}
export default Detail